body {
  --color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  left: 0;
  right: 0;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  text-decoration: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--color);
  position: relative;
}

nav a:hover {
  color: var(--color);
}

.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
  right: 0;
}
